.page-footer {
  background-color: $basic-grey;

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    padding: 20px;

    @media (min-width: $table) {
      padding: 55px 30px 61px;
      flex-direction: row;
      justify-content: space-between;
    }

    @media (min-width: $desktop) {
      padding: 59px 110px 56px;
    }
  }

  &__item {
    display: flex;
    justify-content: center;
    position: relative;
    padding: 20px 0;
    width: 100%;

    @media (min-width: $table) {
      width: auto;
    }
  }

  &__item:not(:last-child)::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: $footer-border-color;

    @media (min-width: $table) {
      background-color: transparent;
    }
  }
}
