.example-comparison {
  position: relative;
  width: 100%;
  height: 100%;
  max-width: 520px;
  overflow: hidden;

  &__item {
    display: block;
  }

  &__item--new {
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    max-width: 520px;
    height: 100%;
  }

  & img {
    width: 100%;
    height: 100%;
  }

  &__before {
    position: absolute;
    width: 50%;
    overflow: hidden;
  }
}
